<template>
  <div class="main-content">
    <div class="card-content">
      <div class="form-list">
        <Form :model="formData" :rules="formDataValidator">
         <FormItem prop="cronValue3">
            <easy-cron style="width: 750px;"  v-model="formData.cronValue3" :exeStartTime="exeStartTime"
              :hideSecond="true"></easy-cron>
          </FormItem>
        </Form>
        <input id="result" type="hidden" v-model="formData.cronValue3">
      </div>
    </div>
  </div>
</template>

<script>
import EasyCron from './easy-cron'
import InputCron from './easy-cron/input-cron'
import CronValidator from './easy-cron/validator'

export default {
  name: 'HelloWorld',
  data () {
    return {
      exeStartTime: '2020-01-01 00:00:00',
      formData: {
        cronValue3: '0 8 * * ?'
      },
      formDataValidator: {
        cronValue3: [
          { validator: CronValidator }
        ]
      }
    }
  },
  methods: {
    remote (val, time, cb) {
      const msg = `remote called: ${val} ${time}`
      console.info(msg)
      setTimeout(() => {
        // get result for e via http...
        const result = msg
        // ...
        // callback
        cb(result)
      }, 200)
    }
  },
  components: {
    EasyCron,
    InputCron
  },
  created () {
    // console.info(aaa)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  scoped>
h1, h2 {
  font-weight: normal;
}
.main-content {
  // background-color: #E0E0E0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.ivu-form-item-content{
  margin-left: 0px !important;
}

.card-content {
  flex-basis: 600px;
}

.form-list {
  margin-top: 20px;
}
</style>



